.main-header {
  width: 100%;
  padding-top: 30px;
  padding-bottom: 35px;
  display: flex;
  font-family: $family-mono;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  z-index: 5;
}

.main-header .container {
  min-width: 1024px;
  max-width: 1222px;
}
